@import url(https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700&display=swap);
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  font-family: "Inter", sans-serif;
  min-height: 100vh;
  background-color: #e5e5e5;
  /* background-image: linear-gradient(
    rgba(1, 43, 105, 0.52),
    rgba(1, 37, 97, 0.72),
    #00235d
  ); */
  background-image: linear-gradient(
    0.08deg,
    rgba(1, 43, 105, 0.52) 7.94%,
    rgba(1, 37, 97, 0.72) 47.74%,
    #00235d 80.02%
  );
  color: #fff;
}

/*app*/
.app {
  margin: 5rem;
}

.app-detail {
  display: flex;
  justify-content: center;
  grid-gap: 1.5rem;
  gap: 1.5rem;
  align-items: center;
  margin-top: 3rem;
}

/*media query*/
@media screen and (max-width: 768px) {
  .app-detail {
    flex-direction: column;
    grid-gap: 1rem;
    gap: 1rem;
  }
}

.topbar {
  display: flex;
  justify-content: space-between;
}

input {
  padding: 1rem;
  outline: none;
  background: transparent;
  border: 1px solid #fff;
  color: #fff;
  border: none;
}

.search {
  border: 1px solid gainsboro;
  border-radius: 0.5rem;
  display: flex;
  align-items: center;
  grid-row-gap: 2;
  row-gap: 2;
  padding: 0.25rem 0.75rem;
  width: 300px;
}

button {
  padding: 1rem 2rem;
}

/*media query*/
@media screen and (max-width: 768px) {
  .topbar {
    flex-direction: column;
    grid-gap: 1rem;
    gap: 1rem;
  }
  button {
    width: 100%;
  }
  input {
    width: 100%;
  }
  .search {
    width: 150px;
  }
}

.bigcard {
  background-color: #17265c;
  background-image: linear-gradient(
    270deg,
    rgba(23, 38, 92, 0.6) -6.38%,
    rgba(2, 110, 189, 0.408) 107.1%
  );
  width: 450px;
  min-height: 22rem;
  border-radius: 10px;
  /* padding: 1rem; */
}

.bigCard:hover {
  transition: 500ms linear;
}

.location {
  margin-left: 60%;
  margin-top: 1rem;
  display: flex;
  align-items: center;
  grid-gap: 0.8rem;
  gap: 0.8rem;
}

.bottom-text {
  display: flex;
  align-items: center;
  padding-left: 20px;
}

.bottom-text img {
  height: 50px;
  width: 50px;
}

.img-container {
  height: 200px;
}

.img-container img {
  height: 100%;
}

.degree {
  font-size: 2.5rem;
  margin-left: 2rem;
}

/*media query*/
@media screen and (max-width: 912px) {
  .bigcard {
    width: 200px;
  }
  .img-container img {
    width: 100%;
    object-fit: contain;
  }
}

/*media query*/
@media screen and (max-width: 768px) {
  .bigcard {
    width: 300px;
  }
  .img-container img {
    width: 150px;
    object-fit: contain;
  }
}

.smallcard {
  background-color: #17265c;
  background-image: linear-gradient(
    270deg,
    rgba(23, 38, 92, 0.6) -6.38%,
    rgba(2, 110, 189, 0.408) 107.1%
  );
  width: 250px;
  height: 22rem;
  border-radius: 10px;
  padding: 1rem;
}

.condition {
  display: flex;
  align-items: center;
  grid-gap: 0.6rem;
  gap: 0.6rem;
}

.condition p {
  font-size: 0.8rem;
  text-transform: uppercase;
}

.time {
  font-size: 2rem;
}

.time-container {
  margin-top: 3rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 70%;
}

/*media query*/
@media screen and (max-width: 912px) {
  .smallcard {
    width: 150px;
  }
  .image-container img {
    width: 100%;
  }
}

/*media query*/
@media screen and (max-width: 768px) {
  .smallcard {
    width: 300px;
  }
}

.forecast-card {
  background-color: #17265c;
  background: linear-gradient(
    360deg,
    rgba(1, 24, 78, 0.444) 15.42%,
    rgba(1, 37, 97, 0.462) 54.1%,
    rgba(0, 29, 87, 0.45) 92.97%
  );
  width: 450px;
  height: 22rem;
  border-radius: 10px;
  padding: 1rem;
}

.forecast-detail {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 1rem;
  grid-gap: 1.6rem;
  gap: 1.6rem;
}

.days {
  border-bottom: 1px solid white;
}

.divided {
  display: flex;
  align-items: center;
}

.divider {
  flex-grow: 1;
  border-bottom: 5px solid #b7ff7e;
  margin: 5px;
  width: 100px;
}

/*media query*/
@media screen and (max-width: 912px) {
  .forecast-card {
    width: 200px;
  }

  img {
    height: 20px;
    width: 20px;
  }

  .divider {
    width: 50px;
  }
}

/*media query*/
@media screen and (max-width: 768px) {
  .forecast-card {
    width: 300px;
  }

  img {
    height: 20px;
    width: 20px;
  }
}

.forecast {
  margin: 3rem 13rem;
  background-image: linear-gradient(
    360deg,
    rgba(1, 24, 78, 0.444) 15.42%,
    rgba(1, 37, 97, 0.462) 54.1%,
    rgba(0, 29, 87, 0.45) 92.97%
  );
  padding: 1rem 2rem;
  border-radius: 1rem;
}

.subHeader {
  width: 100%;
}

.timing-container {
  display: flex;
  justify-content: center;
  align-items: center;
  grid-gap: 2rem;
  gap: 2rem;
  margin-top: 1rem;
}

.timing {
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-gap: 1.5rem;
  gap: 1.5rem;
  justify-content: center;
  margin-bottom: 1rem;
}

.subHeader p {
  text-transform: uppercase;
  /* border-bottom: 1px solid white; */
}

hr {
  border: 1px solid rgba(255, 255, 255, 0.3);
}

.forecast-container {
  /* width: 100%; */
}

/*media query*/
@media screen and (max-width: 912px) {
  .forecast {
    display: none;
  }
}

/*media query*/
@media screen and (max-width: 768) {
  .forecast {
    display: none;
  }
  .timing img {
    width: 100%;
    object-fit: contain;
  }
}

